import apiClient from '../../utils/apiClient';

const LOAD = 'tags/LOAD';
const LOAD_SUCCESS = 'tags/LOAD_SUCCESS';
const LOAD_FAIL = 'tags/LOAD_FAIL';

const initialState = {
  loading: false,
  data: []
};

export default function reducer(state = initialState, action) {
  switch (action.type) {
    case LOAD:
      return {
        ...state,
        loading: true,
      };
    case LOAD_SUCCESS:
      return {
        ...state,
        loading: false,
        data: action.result,
        error: null
      };
    case LOAD_FAIL:
      return {
        ...state,
        loading: false,
        error: action.error,
      };

    default:
      return state;
  }
}

export function loadTags() {
  return {
    types: [LOAD, LOAD_SUCCESS, LOAD_FAIL],
    promise: () => apiClient({
      query: `
      query queryTags {
        user {
          tags {
            id
            name
            _linksMeta {
              count
            }
          }
        }
      }
      `,
      operationName: 'queryTags',
    }).then(({ user }) => user.tags.filter((tag) => tag._linksMeta.count > 0))
  };
}



