import apiClient from '../../utils/apiClient';

const LOAD = 'links/LOAD';
const LOAD_SUCCESS = 'links/LOAD_SUCCESS';
const LOAD_FAIL = 'links/LOAD_FAIL';
const DELETE = 'links/DELETE';
const DELETE_SUCCESS = 'links/DELETE_SUCCESS';
const DELETE_FAIL = 'links/DELETE_FAIL';

const initialState = {
  loading: false,
  data: []
};

export default function reducer(state = initialState, action) {
  switch (action.type) {
    case LOAD:
      return {
        ...state,
        loading: true,
      };
    case LOAD_SUCCESS:
      return {
        ...state,
        loading: false,
        data: action.result,
        error: null
      };
    case LOAD_FAIL:
      return {
        ...state,
        loading: false,
        error: action.error,
      };

    default:
      return state;
  }
}

const queryLinksGql = `
query queryLinks($userId: ID!, $tags: [String!]) {
  allLinks(
    filter: {
      AND: [{
        user: {
          id: $userId
        },
        tags_some: {
          name_in: $tags
        }
      }]
    },
    orderBy: createdAt_DESC
  ) {
    url
    id
    title
    description
    tags {
      id
      name
    }
  }
}`;

const getAllLinksGql = `
query getAllLinks($userId: ID!) {
  allLinks(
    filter: {
      user: {
        id: $userId
      }
    },
    orderBy: createdAt_DESC
  ) {
    url
    id
    title
    description
    tags {
      id
      name
    }
  }
}
`;

export function loadLinks(userId, tags = []) {
  return {
    types: [LOAD, LOAD_SUCCESS, LOAD_FAIL],
    promise: () => apiClient({
      query: tags.length ? queryLinksGql : getAllLinksGql,
      variables: {
        userId,
        tags,
      },
      operationName: tags.length ? 'queryLinks' : 'getAllLinks',
    }).then(({ allLinks }) =>
      tags.length ? allLinks.filter((link) => tags.every((inputTag) => link.tags.map((t) => t.name).includes(inputTag))) : allLinks)
  };
}

export function deleteLink(linkId) {
  return {
    types: [DELETE, DELETE_SUCCESS, DELETE_FAIL],
    promise: () => apiClient({
      query: `
      mutation deleteLink($linkId: ID!) {
        deleteLink(id: $linkId) {
          id
        }
      }
      `,
      operationName: 'deleteLink',
      variables: {
        linkId
      }
    })
  };
}



