import React from 'react';
import { compose } from 'recompose';
import c from 'classnames';
import truncate from 'lodash/truncate';
import closeSvg from '../../assets/close.svg';

export default compose(
)(function Link({ onRemoveRequested, focus, blur, isFocused, id, url, title, description, tags }) {
  return (
    <div
      tabIndex="1"
      className={c('outline-none row')}
      onBlur={blur}
    >
      <div className="col-10">
        <p className="lead mb-0">
          <a href={url} target="_blank">{truncate(title ? title : url)}</a>
        </p>
        <p>
          {description}
        </p>
        <p className="mb-1 lead">
          {tags.map(({ name, id }) =>
            <span className="mr-1 badge badge-primary" key={id}>{name}</span>
          )}
        </p>
      </div>
      <div className="col-2 d-flex justify-content-end align-items-center">
        <img
          className="clickable"
          src={closeSvg}
          alt="close"
          width={40}
          height={40}
          onClick={() => onRemoveRequested(id)}
        />
      </div>
    </div>
  );
})



