/* globals chrome */
import React from 'react';
import ReactDOM from 'react-dom';
import { ChromeExt } from './containers';
import axios from 'axios';
import { createStore, compose, applyMiddleware } from 'redux';
import { Provider } from 'react-redux';
import reducer from './redux/modules/reducer';
import clientMiddleware from './redux/middlewares/clientMiddleware';
import { MemoryRouter } from 'react-router-dom';
import { loadUser } from './redux/modules/auth';
import './App.css';

const middleware = [
  clientMiddleware(),
];

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const store = createStore(
  reducer, /* preloadedState, */
  composeEnhancers(applyMiddleware(...middleware))
);


new Promise((resolve) => {
  try {
    chrome.storage.sync.get(null, (data) => {
      const token = data['taggytag-token'];
      resolve(token);
    });
  } catch(e) {
    resolve(localStorage.getItem('taggytag-token'));
  }
}).then((token) => {
  axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
  store.dispatch(loadUser());
  ReactDOM.render(
    <Provider store={store}>
      <MemoryRouter
        // initialEntries={['/', '/login', '/logout']}
        // initialIndex={0}
      >
        <ChromeExt token={token}/>
      </MemoryRouter>
    </Provider>,
  document.getElementById('root'));
});




