/* globals chrome */
import React from 'react';
import { withHandlers, compose } from 'recompose';
import { connect } from 'react-redux';
import { Card, CardBody,
  CardTitle, Input, Button, FormGroup } from 'reactstrap';
import { login, loadUser } from '../../redux/modules/auth';
import { withRouter } from 'react-router-dom';

export default compose(
  withRouter,
  connect(
    (state) => ({
      auth: state.auth.data
    }),
    {
      login,
      loadUser
    }
  ),
  withHandlers({
    submit: ({ login, loadUser }) => (e) => {
      e.preventDefault();
      const email = e.target.email.value;
      const password = e.target.password.value;
      login(email, password)
        .then((data) => {
          const { token } = data;

          try {
            chrome.storage.sync.set({'taggytag-token': token}, () => {
            });
          } catch(e) {
            localStorage.setItem('taggytag-token', token);
          }
        });
    }
  })
)(function Login({ submit }) {
  return (
    <form className="container" onSubmit={submit}>
      <div className="row">
        <div className="col">
          <Card>
            <CardBody className="text-center">
              <CardTitle>Login</CardTitle>
              <FormGroup>
                <Input name="email" type="text" placeholder="Username"/>
              </FormGroup>
              <FormGroup>
                <Input name="password" type="password" placeholder="Password"/>
              </FormGroup>
              <Button type="submit" color={'success'}>Submit</Button>
            </CardBody>
          </Card>
        </div>
      </div>
    </form>
  );
})



