/* globals chrome */
import React from 'react';
import { withHandlers, compose, withState, lifecycle } from 'recompose';
import get from 'lodash/get';
import apiClient from '../../utils/apiClient';
import { FormGroup, Input, Button } from 'reactstrap';
import { withRouter } from 'react-router-dom';
import axios from 'axios';
import Select from 'react-select';
import { connect } from 'react-redux';
import { loadTags } from '../../redux/modules/tags';
import enterKeySvg from '../../assets/enter-key.svg';

export default compose(
  withRouter,
  connect(
    (state) => ({
      allTags: state.tags.data,
    }),
    {
      loadTags
    }
  ),
  withState('creatingLink', 'updateCreatingLink', false),
  withState('showingSuccessMessage', 'showSuccessMessage', false),
  withHandlers({
    createLink: ({ userId }) => (url, title, description) => apiClient({
      query: `
      mutation createLink($url: String!, $userId: ID!, $title: String, $description: String) {
        createLink(url:$url, userId: $userId, title: $title, description: $description) {
          id
          url
          title
          description
        }
      }
      `,
      operationName: 'createLink',
      variables: { url, userId, title, description }
    }),
    createTag: ({ userId }) => (tagName) => apiClient({
      query: `
      mutation createTag($tagName: String!, $userId: ID!) {
        createTag(name: $tagName, userId: $userId) {
          id
          name
          links {
            id
            url
          }
        }
      }
      `,
      operationName: 'createTag',
      variables: { userId, tagName }
    }),
    addToTagOnLink: () => ({linkId, tagId}) => apiClient({
      query: `
      mutation addToTagOnLink($linkId: ID!, $tagId: ID!) {
        addToTagOnLink(linksLinkId: $linkId, tagsTagId: $tagId) {
          linksLink{id}
          tagsTag{id}
        }
      }
      `,
      operationName: 'addToTagOnLink',
      variables: { linkId, tagId }
    })
  }),
  withState('url', 'updateUrl', ''),
  withState('tags', 'updateTags', []),
  withState('title', 'updateTitle', ''),
  withState('description', 'updateDescription', ''),
  withState('loadingMeta', 'updateLoadingMeta', false),
  withState('gettingUrl', 'updateGettingUrl', true),
  withHandlers({
    onUrlBlur: ({ updateLoadingMeta, updateTitle, updateDescription, url }) => async () => {
      if (!url) return;
      updateLoadingMeta(true);
      try {
        const { data } = await axios.get(`https://cson-metadata-extractor.herokuapp.com?url=${url}`)
        updateTitle(data.title || '');
        updateDescription(data.description || '');
      } catch (e) {
      }
      updateLoadingMeta(false);
    },
    reset: ({ updateTitle, updateTags, updateDescription, updateUrl }) => () => {
      updateUrl('');
      updateTags([]);
      updateTitle('');
      updateDescription('');
    }
  }),
  lifecycle({
    componentDidMount() {
      const { updateGettingUrl, updateUrl, onUrlBlur, loadTags } = this.props;
      loadTags();
      try {
        updateGettingUrl(true);
        chrome.tabs.query({active: true, currentWindow: true}, (tab) => {
          const url = get(tab, '0.url');
          if (url) {
            console.log('URL:', url);
            updateGettingUrl(false);
            updateUrl(url);
            setTimeout(() => {
              onUrlBlur();
            }, 0);
          }
        });
      } catch (e) {
        // Not chrome extension
        updateGettingUrl(false);
      }
    },
  }),
  withHandlers({
    addLink: ({ showSuccessMessage, reset, creatingLink, updateCreatingLink, loadTags, createLink, createTag, addToTagOnLink, userId, queryTags, tags: inputTagString, url, updateUrl, title, description }) => async (e) => {
      e.preventDefault();
      if (creatingLink) return;

      const tagNames = inputTagString.map((tag) => tag.value.trim());
      const { createLink: link } = await createLink(url, title, description);

      let finalTags = [];
      updateCreatingLink(true);
      const { allTags } = await apiClient({
        query: `
          query queryTags($userId: ID!, $tags: [String!]) {
            allTags(filter: { user: { id: $userId }, name_in: $tags}) {
              name
              id
            }
          }`,
        operationName: 'queryTags',
        variables: {
          userId,
          tags: tagNames
        }
      });

      finalTags = [...finalTags, ...allTags];

      if (allTags.length < tagNames.length) {
        const missingTagNames = tagNames.filter(
          (tagStr) => !allTags.map(({ name }) => name).includes(tagStr)
        );
        const missingTags = await Promise.all(missingTagNames.map((name) => createTag(name))).then((results) => results.map(({ createTag }) => createTag));
        finalTags = [...finalTags, ...missingTags];
      }

      await Promise.all(finalTags.map((tag) => {
        return addToTagOnLink({
          linkId: link.id,
          tagId: tag.id
        })
      }));

      updateCreatingLink(false);
      reset();
      showSuccessMessage(true);
      setTimeout(() => showSuccessMessage(false), 4000)

      await loadTags();
    }
  }),
)(function AddLink({ showingSuccessMessage, creatingLink, allTags, gettingUrl, loadingMeta, updateTitle, updateDescription, title, description, onUrlBlur, addLink, AddLink, url, tags, updateUrl, updateTags }) {
  return (
    <form onSubmit={addLink}>
      <FormGroup>
        <Input
          disabled={gettingUrl || creatingLink}
          type="text"
          placeholder="Url"
          value={url}
          onBlur={onUrlBlur}
          onChange={(e) => updateUrl(e.target.value)}/>
      </FormGroup>
      <FormGroup>
        <Select.Creatable
          disabled={creatingLink}
          placeholder="Tags"
          options={allTags.map(({ name }) => ({ value: name, label: name }))}
          multi
          onChange={updateTags}
          value={tags}
        />
      </FormGroup>
      <FormGroup>
        <Input
          disabled={loadingMeta || creatingLink}
          type="text"
          placeholder={loadingMeta ? 'Fetching title...' : 'Title'}
          value={title}
          onChange={(e) => updateTitle(e.target.value)}
        />
      </FormGroup>
      <FormGroup>
        <Input
          disabled={loadingMeta || creatingLink}
          type="text"
          placeholder={loadingMeta ? 'Fetching description' : 'Description'}
          value={description}
          onChange={(e) => updateDescription(e.target.value)}
        />
      </FormGroup>
      <p className="text-success text-center">
        {showingSuccessMessage ? 'You link is bookmarked!' : <span>&nbsp;</span>}
      </p>
      <div className="text-center mb-5">
        <img className="d-inline mr-4" src={enterKeySvg} alt={'Enter Key'} width="50"/>
        Or
        <Button disabled={creatingLink} type="submit" className="btn btn-success ml-4">
          Add
        </Button>
      </div>
    </form>
  );
})



