import axios from 'axios';

export default function apiClient(data) {
  return axios
    .post('https://api.graph.cool/simple/v1/cjb2kuh501pwz0146bk89ad6j', data)
    .then(({ data }) => data)
    .then(({ data, errors }) => {
      if (data) return Promise.resolve(data);
      return Promise.reject(errors);
    });
}



