import apiClient from '../../utils/apiClient';

const LOAD = 'auth/LOAD';
const LOAD_SUCCESS = 'auth/LOAD_SUCCESS';
const LOAD_FAIL = 'auth/LOAD_FAIL';
const LOGIN = 'auth/LOGIN';
const LOGIN_SUCCESS = 'auth/LOGIN_SUCCESS';
const LOGIN_FAIL = 'auth/LOGIN_FAIL';
const LOAD_TOKEN = 'auth/LOAD_TOKEN';
const LOGOUT = 'auth/LOGOUT';

const initialState = {
  loading: false,
  data: {},
  loaded: false,
};

export default function reducer(state = initialState, action) {
  switch (action.type) {
    case LOAD_TOKEN:
      return {
        ...state,
        data: {
          ...state.data,
          token: action.token
        }
      };

    case LOAD:
      return {
        ...state,
        loading: true,
      };
    case LOAD_SUCCESS:
      return {
        ...state,
        loading: false,
        data: action.result,
        loaded: true,
        error: null
      };
    case LOAD_FAIL:
      return {
        ...state,
        loading: false,
        error: action.error,
      };
    case LOGIN_SUCCESS:
      return {
        ...state,
        data: action.result,
        loaded: true,
      };
    case LOGOUT:
      return {
        ...state,
        data: {},
        loaded: false
      };

    default:
      return state;
  }
}

export function login(email, password) {
  return {
    types: [LOGIN, LOGIN_SUCCESS, LOGIN_FAIL],
    promise: () => apiClient({
      query: `
      mutation signIn($email: String!, $password: String!) {
        signinUser(email: { email: $email, password: $password }) {
          user {
            id
            email
          }
          token
        }
      }
      `,
      operationName: 'signIn',
      variables: {
        email,
        password
      }
    }).then(({ signinUser }) => signinUser)
  };
}

export function isAuthLoaded(globalState) {
  return globalState.auth.loaded;
}

export function loadUser(token) {
  return {
    types: [LOAD, LOAD_SUCCESS, LOAD_FAIL],
    promise: () => apiClient({
      query: `
      query currentUser {
        user {
          id
          email
        }
      }
      `,
      operationName: 'currentUser'
    }).then((data) => data.user ?
      Promise.resolve(data) :
      Promise.reject('Unauthorized')
    )
  };
}

export function logout() {
  return {
    type: LOGOUT,
  };
}

export function loadToken(token) {
  return { token };
}



