import React from 'react';
import { Navbar, NavbarBrand, Collapse, Nav, NavItem, NavLink, UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem} from 'reactstrap';
import { withRouter } from 'react-router-dom';
import menuSvg from '../../assets/menu.svg';

function MenuButton(props) {
  return (
    <img
      width={50}
      height={50}
      className="clickable d-sm-none"
      src={menuSvg}
      alt={'menu'}
      {...props}/>
  );
}

class Header extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      isOpen: false
    };
  }
  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    });
  }
  render() {
    const { user, history } = this.props;
    return (
      <Navbar className="bg-light" fixed="top" color="faded" light expand="md">
        <NavbarBrand href="/">TaggyTag</NavbarBrand>
        <MenuButton onClick={this.toggle} />
        <Collapse isOpen={this.state.isOpen} navbar>
          <Nav className="ml-auto" navbar>
            <NavItem>
              <NavLink href="/components/">Google Chrome Extension</NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="/instruction/">How to TaggyTag</NavLink>
            </NavItem>
            {user &&
              <UncontrolledDropdown nav>
                <DropdownToggle nav caret>
                  {user.email}
                </DropdownToggle>
                <DropdownMenu >
                  <DropdownItem onClick={() => {
                    this.toggle();
                    history.push('/logout');
                  }}>
                    Logout
                  </DropdownItem>
                </DropdownMenu>
              </UncontrolledDropdown>
            }
            {!user &&
              <NavItem>
                <NavLink href="/instruction/">Sign In</NavLink>
              </NavItem>
            }
          </Nav>
        </Collapse>
      </Navbar>
    );
  }
}
export default withRouter(Header);



