import React from 'react';
import { compose } from 'recompose';
import get from 'lodash/get';
import { Login, AddLink, LoadingPage, Logout } from '../';
import { Header } from '../../components';
import { connect } from 'react-redux';
import { Route, Redirect, withRouter } from 'react-router-dom';

export default compose(
  withRouter,
  connect(
    (state) => ({
      auth: state.auth.data,
      loading: state.auth.loading,
      isAuthLoaded: state.auth.loaded,
    })
  ),
)(function ChromeExt({ location, isAuthLoaded, auth, loading }) {
  const userId = get(auth, 'user.id');

  return (
    <div className="chrome-ext container have-fixed-navbar">
      <Header user={auth.user}/>
      <Route exact path="/login" render={(routeProps) => {
        if (loading) return <LoadingPage/>;

        if (!isAuthLoaded) return <Login {...routeProps}/>;
        return <Redirect to={{ pathname: '/', state: { from: routeProps.location } }}/>
      }}/>
      <Route exact path="/logout" component={Logout}/>
      <Route exact path="/" render={(routeProps) => {
        if (loading) return <LoadingPage/>;

        if (isAuthLoaded) return <AddLink userId={userId} {...routeProps}/>;
        return <Redirect to={{ pathname: '/login', state: { from: routeProps.location } }}/>
      }}/>
    </div>
  );
})



