import React from 'react';
import { compose, lifecycle, withHandlers, withState } from 'recompose';
import Select from 'react-select';
import flatten from 'lodash/flatten';
import uniqBy from 'lodash/uniqBy';
import { Col, Row } from 'reactstrap';
import c from 'classnames';
import { connect } from 'react-redux';
import { loadLinks, deleteLink } from '../../redux/modules/links';
import { loadUser, isAuthLoaded } from '../../redux/modules/auth';
import reloadSvg from '../../assets/reload.svg';
import { Link } from '../../components';
import get from 'lodash/get';

export default compose(
  connect(
    (state) => ({
      links: state.links.data,
      loading: state.links.loading,
      userId: get(state, 'auth.data.user.id'),
      isUserLoaded: isAuthLoaded(state)
    }),
    {
      loadLinks,
      loadUser,
      deleteLink,
    }
  ),
  withState('inputTags', 'updateInputTags', ''),
  lifecycle({
    componentDidMount() {
      this.props.loadLinks(this.props.userId);
    }
  }),
  withHandlers({
    searchLinks: ({ loadLinks, inputTags, client, userId }) => (e) => {
      e && e.preventDefault();
      const _inputTags = inputTags ? inputTags
        .split(',')
        .map((tag) => tag.trim()) : [];

      loadLinks(userId, _inputTags);
    }
  }),
  withHandlers({
    refresh: ({ searchLinks }) => () => {
      searchLinks();
    },
  }),
  withHandlers({
    deleteLink: ({ refresh, deleteLink }) => async (linkId) => {
      await deleteLink(linkId);

      refresh();
    }
  })
)(function Search({ loading, deleteLink, refresh, allTags, links, inputTags, updateInputTags, searchLinks }) {
  const suggestions = compose(
    (value) => uniqBy(value, 'id'),
    flatten,
  )(links.map(({ tags }) => tags));

  return (
    <div>
      <Select
        name="tags"
        placeholder="Search links by tags"
        options={suggestions.map(({ name }) => ({ value: name, label: name }))}
        multi
        simpleValue
        value={inputTags}
        onChange={(value) => {
          updateInputTags(value);
          setTimeout(() => {
            searchLinks();
          }, 0);
        }}
      />

      <Row className="mt-4">
        <Col className="justify-content-end d-flex">
          <div className={'clickable text-secondary'}>
            REFRESH
            <img
              className={c({ spin: loading }, 'ml-2')}
              onClick={refresh}
              src={reloadSvg}
              alt={'reload'}
              width="30px"
              height="30px"/>
          </div>
        </Col>
      </Row>

      <ul className="list-group mt-4">
        {links && links.map((link) =>
          <li className="list-group-item p-relative" key={link.id}>
            <Link
             {...link}
             onRemoveRequested={deleteLink}
            />
          </li>
        )}
      </ul>
    </div>
  );
})



